export class Stack {
  constructor() {
    this.items = [];
  }

  size() {
    return this.items.length;
  }

  pop() {
    if (!this.isEmpty()) return this.items.pop();
    throw new Error("Underflow error");
  }

  push(item) {
    this.items.push(item);
  }

  isEmpty() {
    return this.items.length == 0;
  }

  peek() {
    if (!this.isEmpty()) return this.items[this.items.length - 1];
    throw new Error("Empty Stack");
  }

  getItemsAsArray() {
    return this.items;
  }
}

export class Queue {
  constructor(max_size) {
    this.items = [];
    this.max_size = max_size;
  }

  enqueue(item) {
    if (!this.isFull()) this.items.unshift(item);
    throw new Error("Overflow error");
  }

  dequeue() {
    return this.items.pop();
  }

  size() {
    return this.items.length;
  }

  isEmpty() {
    return this.items.length == 0;
  }

  isFull() {
    return this.items.length == this.max_size;
  }

  getItemsAsArray() {
    return this.items;
  }
}

export class SingleNode {
  constructor(value, next_node) {
    this.value = value;
    this.next = next_node;
  }
}

export class DoubleNode {
  constructor(value, next_node, previous_node) {
    this.value = value;
    this.next = next_node;
    this.previous_node = previous_node;
  }
}

export class SLinkedList {
  constructor(head) {
    this.head = head;
  }
}
