<template>
  <v-app>
    <v-content>
      <v-card height="100vh" flat class="pa-0 d-flex flex-column justify-start">
        <v-toolbar dark dense class="flex-grow-0" elevation="1" color="primary">
          <v-toolbar-title>STACK</v-toolbar-title>
          <template v-slot:extension>
            <v-row>
              <v-col class="sm-6">
                <v-tabs background-color="primary" v-model="tab1">
                  <!-- <v-tab href="#theory-tab" class="text-capitalize"
                    >Theory</v-tab
                  > -->
                  <v-tab href="#code-tab" class="text-capitalize">Code</v-tab>
                  <v-tab href="#playin-tab" class="text-capitalize"
                    >Playground IN</v-tab
                  >
                </v-tabs>
              </v-col>
              <v-col class="sm-6">
                <v-tabs background-color="primary" centered v-model="tab2">
                  <v-tab href="#playout-tab" class="text-capitalize"
                    >Playground OUT</v-tab
                  >
                </v-tabs>
              </v-col>
            </v-row>
          </template>
        </v-toolbar>
        <v-card flat class="flex-grow-1">
          <v-row no-gutters class="fill-height">
            <v-col sm="6">
              <v-card height="100%" flat class="pa-2">
                <v-tabs-items v-model="tab1">
                  <!-- <v-tab-item :value="'theory-tab'">Theory</v-tab-item> -->
                  <v-tab-item :value="'code-tab'">
                    <v-card class="pa-4 overflow-x-auto">
                      <pre id="code">
                        {{
                          `
class Stack {
  constructor() {
    this.items = [];
  }

  size() {
    return this.items.length;
  }

  pop() {
    if (!this.isEmpty()) return this.items.pop();
    throw new Error("Underflow error");
  }

  push(item) {
    this.items.push(item);
  }

  isEmpty() {
    return this.items.length == 0;
  }

  peek() {
    if (!this.isEmpty()) return this.items[this.items.length - 1];
    throw new Error("Empty Stack");
  }

  getItemsAsArray() {
    return this.items;
  }
}
`
                        }}
                      </pre>
                    </v-card>
                  </v-tab-item>
                  <v-tab-item :value="'playin-tab'">
                    <v-card flat class="d-flex justify-space-around pa-4">
                      <v-btn @click="pop" color="primary" class="text-none"
                        >pop()</v-btn
                      >
                      <v-btn @click="peek" color="primary" class="text-none"
                        >peek()</v-btn
                      >
                      <v-btn @click="allItems" color="primary" class="text-none"
                        >allItems()</v-btn
                      >
                      <v-btn @click="size" color="primary" class="text-none"
                        >size()</v-btn
                      >
                      <v-btn @click="isEmpty" color="primary" class="text-none"
                        >isEmpty()</v-btn
                      >
                    </v-card>

                    <v-card flat class="pa-4">
                      <v-text-field
                        @keyup.enter="push"
                        label="Item To Add"
                        outlined
                        v-model="item"
                      ></v-text-field>
                      <v-card flat class="d-flex justify-end">
                        <v-btn @click="push" color="primary" class="text-none"
                          >push(item)</v-btn
                        >
                      </v-card>
                    </v-card>
                  </v-tab-item>
                </v-tabs-items>
              </v-card>
            </v-col>
            <v-col sm="6" flat outlined class="fixed">
              <v-card
                outlined
                ref="outputView"
                flat
                class="d-flex flex-column fill-height overflow-y-auto px-4"
              >
                <v-toolbar flat dense class="flex-grow-0 pa-0">
                  <!-- <v-chip small class="mx-auto">Output</v-chip> -->
                  <v-btn @click="output = []" color="info" class="ml-auto"
                    >CLEAR OUTPUT</v-btn
                  >
                </v-toolbar>
                <v-card
                  flat
                  class="d-flex flex-column-reverse justify-end fill-height overflow-y-auto"
                >
                  <template>
                    <div v-for="(data, index) in output" :key="index">
                      <v-card flat class="d-flex mb-3">
                        <v-card class="d-flex">
                          <v-card
                            flat
                            dark
                            :color="data.isError ? 'error' : 'primary'"
                            class="px-4 py-1"
                          >
                            {{ data.operation }}
                          </v-card>
                          <v-card
                            v-if="
                              data.result !== undefined &&
                                data.operation !== 'getItems()'
                            "
                            flat
                            class="px-10 d-flex justify-center align-center"
                          >
                            {{ data.result }}
                          </v-card>
                        </v-card>
                      </v-card>
                      <v-card
                        flat
                        class="d-flex mb-3 flex-wrap"
                        v-if="data.operation === 'getItems()'"
                      >
                        <v-card dark color="success" class="pa-2 mb-3 mr-3">
                          Bottom
                        </v-card>
                        <v-card
                          v-if="data.result.length === 0"
                          class="pa-2 mb-3 mr-3 px-4"
                        >
                          -
                        </v-card>
                        <v-card
                          v-for="(item, index) in data.result"
                          :key="index"
                          class="pa-2 mb-3 mr-3 px-4"
                        >
                          {{ item }}
                        </v-card>
                        <v-card dark color="success" class="pa-2 mb-3 mr-3">
                          Top
                        </v-card>
                      </v-card>
                      <v-divider class="mb-3"></v-divider>
                    </div>
                  </template>
                </v-card>
              </v-card>
            </v-col>
          </v-row>
        </v-card>
      </v-card>
    </v-content>
  </v-app>
</template>

<script>
import { Stack } from "./structures";

export default {
  name: "App",
  data: () => ({
    tab1: "playin-tab",
    stack: new Stack(),
    tab2: null,
    item: null,
    output: [],
    maxOutputSize: 10
  }),
  methods: {
    pop() {
      try {
        this.output.push({ operation: "pop()", result: this.stack.pop() });
      } catch (err) {
        this.output.push({ operation: "pop()", result: err, isError: true });
      } finally {
        this.regulateOutput();
      }
    },
    peek() {
      try {
        this.output.push({ operation: "peek()", result: this.stack.peek() });
      } catch (err) {
        this.output.push({
          operation: "peek()",
          result: err,
          isError: true
        });
      } finally {
        this.regulateOutput();
      }
    },
    push() {
      if (!this.item) return;
      this.stack.push(this.item);
      this.output.push({
        operation: `push(${this.item})`
      });
      this.item = null;
      this.regulateOutput();
    },
    size() {
      this.output.push({ operation: "size()", result: this.stack.size() });
      this.regulateOutput();
    },
    isEmpty() {
      this.output.push({
        operation: "isEmpty()",
        result: this.stack.isEmpty()
      });
      this.regulateOutput();
    },
    allItems() {
      this.output.push({
        operation: "getItems()",
        result: [...this.stack.getItemsAsArray()]
      });
      this.regulateOutput();
    },
    regulateOutput() {
      if (this.output.length > this.maxOutputSize) {
        this.output.shift();
      }
    }
  }
};
</script>

<style>
#code {
  font-family: "Roboto Mono";
}
</style>
